



.post {
  width: 355px;
  margin: 0px 25px 40px 25px;
  display: flex;
  flex-direction: column;
  /* box-sizing: border-box; */
  border:1px solid #a7a4a4;
  padding:5px 5px;
}
.post:hover{
  /* transition:border-radius; */
  transition-property: border-radius,transform;
  border-radius: 15px;
  transform: translateY(-10px);
  transition-duration: 1s;
  transition-delay: 0;



  
}

.postImg {
  width: 350px;
  height: 280px;
  object-fit: cover;
  border-radius: 7px;
}

.postInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.postCat {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 11px;
  color: #be9656;
  line-height: 19px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

.postTitle {
  font-family: "Josefin Sans", Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 900;
  margin-top: 15px;
  cursor: pointer;
}

.postDate {
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 13px;
  font-weight: 400;
  color: #999999;
  margin-top: 15px;
}

.postDesc {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #444444;
  margin-top: 15px;
  overflow: hidden;
  /*  TODO : BUNDAN SONRAKI CSS NE ISE YARADIGINI BI ARA ANLAMADIK CUNKU */
  /* cardlar yan yana gelmesi icin yariyor  */
  text-overflow: ellipsis;
  display: -webkit-box; 
   -webkit-line-clamp: 4; 
   -webkit-box-orient: vertical;
}
